/**
 * 判断元素是否存在于数组中
 * @author  韦胜健
 * @date    2019/10/18 21:26
 */
export function oneOf(array: any[], target: any | any[]): boolean {
    if (!array || array.length === 0) return false

    let targetList: any[]
    if (typeOf(target) === 'array') {
        targetList = target as any[]
    } else {
        targetList = [target as any]
    }

    return array.some(arrayItem => targetList.some(targetItem => (targetItem == arrayItem)))
}

(window as any).oneOf = oneOf

/**
 * 判断类型
 * @author  韦胜健
 * @date    2019/10/18 21:36
 */
const TypeMap = {
    '[object Boolean]': 'boolean',
    '[object Number]': 'number',
    '[object String]': 'string',
    '[object Function]': 'function',
    '[object Array]': 'array',
    '[object Date]': 'date',
    '[object RegExp]': 'regExp',
    '[object Undefined]': 'undefined',
    '[object Null]': 'null',
    '[object Object]': 'object',
    '[object Promise]': 'promise',
}
const toString = Object.prototype.toString

function isValidType(data: string): data is keyof typeof TypeMap {
    return data in TypeMap
}

export function typeOf(obj: any): string | null {
    const type = toString.call(obj)
    if (isValidType(type)) return TypeMap[type]
    else {
        console.log('unrecognise type:' + type)
        return null
    }
}