import {oneOf, typeOf} from "./utils";

/**
 * 判断元素是否包含某个class
 * @author  韦胜健
 * @date    2019/10/18 22:02
 */
export function hasClass(el: HTMLElement, cls: string[] | string): boolean {
    let clazz: string[]
    if (typeOf(cls) === 'string') {
        clazz = [cls as string]
    } else {
        clazz = cls as string[]
    }

    if (!!el.classList && !!el.classList.value) {
        return oneOf(clazz, el.classList.value.split(' '))
    } else {
        return oneOf(clazz, ((el.className || '') as string).split(' '))
    }
}

/**
 * 给元素添加样式
 * @author  韦胜健
 * @date    2019/10/18 22:02
 */
export function addClass(el: HTMLElement, addCls: string | string[]): void {
    if (!el || !addCls) return
    let addClazz: string[]
    if (typeOf(addCls) === 'string') {
        addClazz = (addCls as string).split(' ')
    } else {
        addClazz = addCls as string[]
    }
    if (!!el.classList && !!el.classList.value) {
        addClazz.forEach(clazz => el.classList.add(clazz))
    } else {
        let currentClassList = ((el.className || '') as string).split(' ')
        addClazz.forEach(addClz => {
            if (currentClassList.indexOf(addClz) === -1) currentClassList.push(addClz)
        })
        el.className = currentClassList.filter(cln => !!cln).join(' ')
    }
}

export function removeClass(el: HTMLElement, emCls: string | string[]): void {
    if (!el || !emCls) return
    let rmClazz: string[]
    if (typeOf(emCls) === 'string') {
        rmClazz = (emCls as string).split(' ')
    } else {
        rmClazz = emCls as string[]
    }
    if (!!el.classList && !!el.classList.value) {
        rmClazz.forEach(clazz => el.classList.remove(clazz))
    } else {
        let currentClassList = ((el.className || '') as string).split(' ')
        for (let i = 0; i < currentClassList.length; i++) {
            const curCls = currentClassList[i];
            if (rmClazz.indexOf(curCls) > -1) {
                currentClassList.splice(i)
                i--
            }
        }
        el.className = currentClassList.filter(cln => !!cln).join(' ')
    }
}