/**
 * 继承属性
 * @author  韦胜健
 * @date    2019/11/10 17:52
 */
export const extend = (to, from) => {
    const ret = deepcopy(to);
    for (const key in from) {
        ret[key] = from[key];
    }
    return ret;
};
/**
 * 判断变量类型
 * @author 韦胜健
 * @date 2018/11/19
 */
export const typeOf = (obj) => {
    const toString = Object.prototype.toString;
    const map = {
        '[object Boolean]': 'boolean',
        '[object Number]': 'number',
        '[object String]': 'string',
        '[object Function]': 'function',
        '[object Array]': 'array',
        '[object Date]': 'date',
        '[object RegExp]': 'regExp',
        '[object Undefined]': 'undefined',
        '[object Null]': 'null',
        '[object Object]': 'object',
        '[object Promise]': 'promise',
    };
    const desc = toString.call(obj);
    if (map.hasOwnProperty(desc)) {
        // @ts-ignore
        return map[desc];
    }
    else {
        console.log('unable to recognise type:' + desc);
        return null;
    }
};
/**
 * 深度复制数据
 * @author  韦胜健
 * @date    2019/11/5 20:23
 */
export const deepcopy = (data) => {
    switch (typeOf(data)) {
        case 'array':
            return data.map((item) => deepcopy(item));
        case 'object':
            return Object.keys(data).reduce((ret, key) => {
                ret[key] = deepcopy(data[key]);
                return ret;
            }, {});
        case 'date':
            const newDate = new Date();
            newDate.setTime(data.getTime());
            return newDate;
        case 'regexp':
            let pattern = data.valueOf();
            let flags = '';
            flags += pattern.global ? 'g' : '';
            flags += pattern.ignoreCase ? 'i' : '';
            flags += pattern.multiline ? 'm' : '';
            return new RegExp(pattern.source, flags);
        default:
            return data;
    }
};
/**
 * 判断是否为合法的数字
 * @author  韦胜健
 * @date    2019/11/5 20:29
 */
export const isNumber = (val) => {
    if (val === '' || val == null)
        return false;
    return !isNaN(val);
};
/**
 * 打乱数组
 * @author  韦胜健
 * @date    2019/1/10 10:56
 */
export const shuffle = (array) => {
    if (!array)
        return array;
    array = [...array];
    let currentIndex = array.length;
    let temporaryValue, randomIndex;
    while (0 !== currentIndex) {
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
    }
    return array;
};
/**
 * 从数组中查找一个
 * @author  韦胜健
 * @date    2019/1/10 10:56
 */
export const findOne = (array, fn, index = false) => {
    for (let i = 0; i < array.length; i++) {
        const item = array[i];
        if (!!fn(item, i)) {
            return !index ? item : { item, index: i };
        }
    }
    return null;
};
/**
 * 从数组中查找多个
 * @author  韦胜健
 * @date    2019/1/10 10:57
 */
export const findSome = (array, fn) => {
    const ret = [];
    for (let i = 0; i < array.length; i++) {
        const item = array[i];
        if (!!fn(item))
            ret.push(item);
    }
    return ret;
};
/**
 * 插入排序
 * @author 韦胜健
 * @date 2018/11/19
 */
export const insertSort = (arr, func) => {
    let len = arr.length;
    for (let i = 1; i < len; i++) {
        let temp = arr[i];
        /*默认已排序的元素*/
        let j = i - 1;
        /*在已排序好的队列中从后向前扫描*/
        while (j >= 0 && func(arr[j], temp)) {
            /*已排序的元素大于新元素，将该元素移到一下个位置*/
            arr[j + 1] = arr[j];
            j--;
        }
        arr[j + 1] = temp;
    }
    return arr;
};
/**
 * 判断是否存在于数组中
 * @author 韦胜健
 * @date 2018/11/19
 */
export const oneOf = (array, value) => {
    return array.indexOf(value) > -1;
};
/**
 * 在数组中移除某一项
 * @author  韦胜健
 * @date    2019/11/5 21:02
 */
export const removeSome = (array, fn) => {
    let count = 0;
    if (!array)
        return count;
    for (let i = 0; i < array.length; i++) {
        const item = array[i];
        if (fn(item, i)) {
            array.splice(i, 1);
            i--;
            count++;
        }
    }
    return count;
};
/**
 * 数组分块
 * @author  韦胜健
 * @date    2019/11/5 21:06
 */
export const chunk = (array, size) => {
    size = Math.max(size, 0);
    const length = array == null ? 0 : array.length;
    if (!length || size < 1) {
        return [];
    }
    let index = 0;
    let resIndex = 0;
    const result = new Array(Math.ceil(length / size));
    while (index < length) {
        result[resIndex++] = array.slice(index, index += size);
    }
    return result;
};
const ObjectUtils = {
    extend,
    typeOf,
    deepcopy,
    isNumber,
    oneOf,
    shuffle,
    findOne,
    findSome,
    insertSort,
    removeSome,
    chunk,
};
export default ObjectUtils;
