import {IceWrapper} from "../utils/ice";
import {addClass} from "../utils/dom";

function getItem(index: number): string {
    return `
            <div class="plain-loading-tag plain-loading-tag-${index + 1}">
                <div class="plain-loading-inner plain-loading-inner-${index + 1}"></div>
            </div>
        `.trim();
}

export default function (): HTMLElement {
    const innerHTML = `
        ${[0, 1, 2, 3, 4].map(index => getItem(index)).join('\n')}
    `.trim();
    const el = IceWrapper(innerHTML)
    addClass(el, 'plain-loading-gamma')
    return el
}