/**
 * 是否包含某种样式class
 * @author 韦胜健
 * @date 2018/11/19
 */
import { oneOf } from "../utils/object";
/**
 * 判断元素是否拥有某个cls
 * @author  韦胜健
 * @date    2019/11/5 21:10
 */
export const hasClass = (el, cls) => {
    if (!!el.classList && !!el.classList.value)
        return oneOf(el.classList.value.split(' '), cls);
    else
        return (el.className || '').split(' ').indexOf(cls) > -1;
};
/**
 * 添加class
 * @author 韦胜健
 * @date 2018/11/19
 */
export const addClass = (el, addCLs) => {
    if (!el || !addCLs)
        return;
    let addClasses;
    if (Array.isArray(addCLs)) {
        addClasses = addCLs;
    }
    else {
        addClasses = addCLs.split(' ');
    }
    if (!!el.classList && !!el.classList.value) {
        addClasses.forEach(item => el.classList.add(item));
    }
    else {
        const curClasses = (el.className || '').split(' ');
        addClasses.forEach(item => {
            if (curClasses.indexOf(item) === -1)
                curClasses.push(item);
        });
        el.className = curClasses.join(' ');
    }
};
/**
 * 删除class
 * @author 韦胜健
 * @date 2018/11/19
 */
export const removeClass = (el, rmCls) => {
    if (!el || !rmCls)
        return;
    let rmClasses;
    if (Array.isArray(rmCls)) {
        rmClasses = rmCls;
    }
    else {
        rmClasses = rmCls.split(' ');
    }
    if (!!el.classList && !!el.classList.value) {
        rmClasses.forEach(item => el.classList.remove(item));
    }
    else {
        const curClasses = (el.className || '').split(' ');
        for (let i = 0; i < rmClasses.length; i++) {
            const rmClass = rmClasses[i];
            const index = curClasses.indexOf(rmClass);
            if (index > -1) {
                curClasses.splice(index, 1);
                i--;
            }
        }
        el.className = curClasses.join(' ');
    }
};
/**
 * 在元素节点后面添加新的节点
 * @author 韦胜健
 * @date 2018/11/19
 */
export const insertAfter = (newEl, targetEl) => {
    let parentEl = targetEl.parentNode;
    if (!!parentEl) {
        if (parentEl.lastChild === targetEl) {
            // 如果最后的节点是目标元素，则直接添加。因为默认是最后
            parentEl.appendChild(newEl);
        }
        else {
            //如果不是，则插入在目标元素的下一个兄弟节点 的前面。也就是目标元素的后面
            parentEl.insertBefore(newEl, targetEl.nextSibling);
        }
    }
};
/*
 *  禁用全局选择文字功能
 *  @author     martsforever
 *  @datetime   2018/12/28 20:56
 */
export const enableUserSelect = () => {
    // @ts-ignore
    document.body.style.userSelect = null;
    delete document.body.style.userSelect;
};
/*
 *  启用全局选择文字功能
 *  @author     martsforever
 *  @datetime   2018/12/28 20:56
 */
export const disabledUserSelect = () => {
    document.body.style.userSelect = 'none';
};
/**
 * 复制内容到剪切板
 * @author  韦胜健
 * @date    2019/4/4 15:51
 */
export const copyToClipboard = (text, success, error) => {
    const textArea = document.createElement("textarea");
    textArea.style.position = 'fixed';
    textArea.style.top = '0';
    textArea.style.left = '0';
    textArea.style.width = '2em';
    textArea.style.height = '2em';
    textArea.style.padding = '0';
    textArea.style.border = 'none';
    textArea.style.outline = 'none';
    textArea.style.boxShadow = 'none';
    textArea.style.opacity = '0';
    textArea.style.zIndex = '-1000';
    textArea.value = text;
    document.body.appendChild(textArea);
    textArea.select();
    try {
        const successful = document.execCommand('copy');
        if (successful)
            !!success && success();
        else
            !!error && error("复制到剪切板失败！");
    }
    catch (err) {
        !!error && error(err);
    }
    document.body.removeChild(textArea);
};
const DomUtils = {
    hasClass,
    addClass,
    removeClass,
    insertAfter,
    enableUserSelect,
    disabledUserSelect,
    copyToClipboard,
};
export default DomUtils;
