import {IceWrapper} from "../utils/ice";
import {addClass} from "../utils/dom";

function getUse(): string {
    return [0, 1, 2, 3, 4, 5].map(index => `<use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#bar" transform="rotate(${index * 60},60,60)"></use>`).join('\n')
}

export default function (loading: boolean = true): HTMLElement {
    const innerHTML = `
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 120 120" class="${!loading ? 'plain-loading-ice-inactive' : ''}">
                    ${getUse()}
                    <symbol id="bar">
                        <polyline points="60,8 60,30 70,15 60,30 50,15 60,30 60,60 60,40 70,30 60,60" style="fill:none"></polyline>
                    </symbol>
                </svg>
        `.trim();
    const el = IceWrapper(innerHTML)
    addClass(el, 'plain-loading-ice')
    return el
}
